import SearchHeader from '@/components/seach-header/seach-header'
import { getNearby } from '@/assets/js/amap'
import nativeBridge from '@/assets/js/nativeBridge'

export default {
  data () {
    return {
      position: [],
      addressList: [
        {
          id: 0,
          publishAddress: '不显示位置'
        }
      ]
    }
  },
  components: {
    SearchHeader
  },
  created () {
    this.search('')
  },
  methods: {
    search (value) {
      const _self = this
      nativeBridge.getPosition(this, (arg) => {
        _self.position = [arg.longitude, arg.latitude]
        getNearby({
          position: _self.position,
          keywords: value
        }, (data) => {
          _self.addressList = [{id: 0, name: '不显示位置'}].concat(data)
        }, (msg) => {
          _self.$vux.toast.text(msg, 'middle')
        })
      })
    },
    getAddress (item) {
      if (item.id === 0) {
        this.$parent.address = null
      } else {
        this.$parent.address = {
          address: item.address + item.name,
          longitude: item.location.split(',')[0],
          latitude: item.location.split(',')[1]
        }
      }
      this.$router.go(-1)
    }
  }
}



